@charset "UTF-8";
@import url("https://fonts.cdnfonts.com/css/helvetica-neue-55");
@font-face {
  font-family: "sunshine-bold";
  src: url("../../assets/fonts/gwangyang-sunshine-bold.ttf") format("truetype");
}
@font-face {
  font-family: "sunshine-regular";
  src: url("../../assets/fonts/gwangyang-sunshine-regular.ttf") format("truetype");
}
.sunshine-bold {
  font-family: "sunshine-bold", sans-serif;
}

.sunshine-regular {
  font-family: "sunshine-regular", sans-serif;
}

* {
  padding: 0;
  margin: 0;
  font-family: "Helvetica Neue", sans-serif;
  -ms-overflow-style: none; /* 인터넷 익스플로러 */
  scrollbar-width: none; /* 파이어폭스 */
}

/* ( 크롬, 사파리, 오페라, 엣지 ) 동작 */
*::-webkit-scrollbar {
  display: none;
}

.v-spacer-32 {
  height: 32px;
}

.v-spacer-64 {
  height: 64px;
}

.v-spacer-96 {
  height: 96px;
}

.h-spacer-16px {
  width: 16px;
}

/* ( 크롬, 사파리, 오페라, 엣지 ) 동작 */
.scroll::-webkit-scrollbar {
  display: none;
}

.scroll {
  -ms-overflow-style: none; /* 인터넷 익스플로러 */
  scrollbar-width: none; /* 파이어폭스 */
}

.header {
  background-color: #fefefe;
  width: 100%;
  height: 64px;
  padding: 8px 8px;
  box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.25);
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-shrink: 0;
  z-index: 1;
  transition: 1s;
}
.header .header-title {
  font-size: 24px;
  transition: 0.5s;
}
.header .header-title:hover {
  color: #7a7777;
}
.header .right-menu {
  display: flex;
  margin: 8px;
  align-items: center;
  gap: 16px;
}
.header .right-menu p:hover {
  background-color: #e0e0e0;
}
.header p {
  padding: 8px;
  border-radius: 2px;
  transition: 0.5s;
  cursor: pointer;
}

.header-sticky {
  position: fixed;
  width: 100%;
  top: 0;
}

