@import "../scss/commons.scss";

.header {
  background-color: $white;
  width: 100%;
  height: 64px;
  padding: 8px 8px;
  box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.25);
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-shrink: 0;
  z-index: 1;

  transition: 1s;

  .header-title {
    font-size: 24px;
    transition: 0.5s;
  }
  .header-title:hover {
    color: $mono600;
  }

  .right-menu {
    display: flex;
    margin: 8px;
    align-items: center;
    gap: 16px;
    p:hover {
      background-color: $mono100;
    }
  }
  p {
    padding: 8px;
    border-radius: 2px;
    transition: 0.5s;
    cursor: pointer;
  }
}

.header-sticky {
  position: fixed;
  width: 100%;
  top: 0;
}
